<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <i class="cui-user icons font-lg"></i> Orders
          </div>
          <b-table
            responsive="sm"
            :items="orders"
            :fields="fields"
            :small="true"
            selectable
            select-mode="single"
            :busy="isLoadingData"
          >
            <template v-slot:cell(name)="data">
              <a
                style="font-size: 15px"
                :href="buildRoute(data.item)"
                variant="link"
              >{{data.item.name}}</a>
            </template>
            <template v-slot:cell(dateCreated)="data">{{data.item.dateCreated.toDate() | date}}</template>
            <template v-slot:cell(quantity)="data">{{sumQuantity(data.item)}}</template>
            <template v-slot:cell(total)="data">{{sumTotal(data.item) | currency(vndConfig)}}</template>
          </b-table>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->
  </div>
</template>

<script>
import fb from "firebase";

export default {
  name: "Orders",
  components: {},
  data() {
    return {
      orders: [],
      counter: {},
      selectedOrder: {},
      isLoadingData: false,
      fields: [
        { key: "name" },
        { key: "dateCreated", label: "Created Date" },
        { key: "quantity", label: "Quantity" },
        { key: "total", label: "Total" }
      ]
    };
  },
  mounted: function() {
    this.loadOrders();
    this.loadCounter();
  },
  computed: {},
  methods: {
    loadCounter: function() {
      this.$fb.counterCollection.limit(1).onSnapshot(snapshot => {
        if (!snapshot.empty)
          snapshot.forEach(doc => {
            this.counter = doc.data();
            this.counter.id = doc.id;
          });
      });
    },
    loadOrders: function() {
      this.orders = [];
      this.isLoadingData = true;
      this.$fb.orderCollection
        .orderBy("dateCreated", "desc")
        .limit(10)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            snapshot.forEach(doc => this.orders.push(doc.data()));
          }
        })
        .then(() => (this.isLoadingData = false));
    },
    sumQuantity: function(order) {
      if (!order.items || order.items.length == 0) return 0;
      return order.items.reduce((sum, x) => (sum += x.quantity), 0);
    },
    sumTotal: function(order) {
      if (!order.items || order.items.length == 0) return 0;
      return order.items.reduce(
        (sum, x) => (sum += x.quantity * parseFloat(x.rndPrice || x.product.rndPrice)),
        0
      );
    },
    buildRoute: function(item) {
      var { origin } = location;
      return this.$router.resolve({
        name: "OrderDetailShipment",
        params: { id: item.id }
      }).href;
    }
  }
};
</script>
