import Vue from 'vue'
var appSettings = Vue.appSettings;

export function productPredictive(product) {
    var term = product.cwProductId || product.name;

    return Vue.$http.get(appSettings.cwCheckPriceApi.replace("{term}", term)).then(res => {
        if (res && res.status == 200 && res.data) {
            var data = res.data;
            var validData = _.filter(data, item => parseFloat(item.id) > 0);
            if (validData) {
                validData = _.map(validData, decorateData(item, product));
                return validData;
            }
            else { return [] }

        }
    });
}

function decorateData(item, product) {
    item.price = parseFloat(item.Price);
    item.thumbnail = appSettings.cwProductThumbnailUrl150.replace("{id}", item.id);
    var price = item.price;
    var cwPrice = parseFloat(product.cwPrice);

    if (price > cwPrice + 0.5) {
        item.priceStatus = "greater";
    } else if (price < cwPrice - 0.5) {
        item.priceStatus = "less";
    } else {
        item.priceStatus = "equal";
    }

    item.hasChanged = item.thumbnail != product.thumbnail || item.priceStatus != "equal";
    return item;
}